body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #cccccc !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

.App{
  background-color: #cccccc;
  color: #19535f;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.App-header {
  background-color: #cccccc;
  min-height: 100vh;
  display: inline-flex;
  flex-direction: column;
  align-items: justify;
  justify-content: center;
  color: #19535F;
  font-size: 2.5vw;
  width: 100%;
}

.App-subtitle{
  font-size: 1.8vw;
}

.App-subsubtitle{
  font-size: 1.5vw;
}

.App-link {
  color: #119da4;
}

.App-header p{
  padding-left: 12vw;
  padding-right: 12vw;
  text-align: center;
}
.profile_img{
	max-width: 15vw;
	max-height: 15vw;
	border-radius: 65%;
}

.person_img{
	float:left;
}

.person_description{
	overflow: auto;
	padding-top: 3.5vw;
	padding-left: 10vw;
	display: inline-block;

}

.user_specific_property{
	font-size: 2vw;
}

.personal_information{
	overflow: auto;
}

.weigth{
	text-align: left;
}

.timerDiv{
	border-style: groove;
	border-width: 5px;
	border-color: #119DA4;
	display:inline-flex;
	flex-direction: row-reverse;
	align-items: center;
	justify-content: flex-start;
	background-color: #282c44;
	max-width: 10vw;
	padding-left: 1vw;
	padding-right: 1vw;
	position: fixed;
	right: 0;
}

.timer{
	display: block;
	padding: 0vw !important;
	color: white !important;
}

.person_description p{
	text-align: left;
	padding-left: 5vw;
	max-width: 95%;
}

.img_test{
	display: inline-flex;
}

.element_test .profile_img{
	margin: 4vw;
	cursor: pointer;
}

.element_test .name{
	margin: 4vw;
	cursor: pointer;
	font-size: 4vw;
}

.element_test .headline{
	margin: 4vw;
	cursor: pointer;
	font-size: 4vw;
}
.aspirations{
	background-color: rgba(204,204,204,0.5);
	width: 100%;
}

.aspiration_elements{
	-webkit-column-count: 2;
	        column-count: 2;
}

.achievements{
	background-color: rgba(204,204,204,0.7);
	width: 100%;
}

.achievement_elements{
	-webkit-column-count: 2;
	        column-count: 2;
}
.jobs{
	background-color: rgba(204,204,204,0.1);
	width: 100%;
}

.job_elements{
	-webkit-column-count: 2;
	        column-count: 2;
}
.education{
	background-color: rgba(204,204,204,0.4);
	width: 100%;
}

.education_elements{
	-webkit-column-count: 2;
	        column-count: 2;
}
.strengths{
	background-color: rgba(204,204,204,0.6);
	width: 100%;
}

.strength_elements{
	-webkit-column-count: 2;
	        column-count: 2;
}
.right_answers{
	overflow: auto;
	max-width: 70%;
	display: inline-block;
}

.wrong_answers{
	overflow: auto;
	max-width: 70%;
	display: inline-block;
}

.profile_img{
	float: right;
}
.element{
	margin: 4vw;
	cursor: pointer;
	font-size: 4vw;
}
